<template>
  <div>
    <h1>Vue To-do Demo</h1>
    <div class="input__todos">
      <input v-model="message" placeholder="Enter To-do" />
    </div>
    <div class="button__add">
      <button @click.prevent="addTodo" class="act__add">Add To-do</button>
    </div>
    <div class="button__rmvdone">
      <button @click.prevent="allDone" class="act__btn">Mark All done</button>
      <button @click.prevent="removeAll" class="act__btn">Remove All</button>
    </div>
    <div
      class="todo__list"
      :class="{ doe: todo.done }"
      v-for="(todo, index) in todos"
      :key="todo.id"
      @click="toggleDone(todo)"
    >
      <h5>{{ index + 1 }}. {{ todo.content }}</h5>
      <button class="btn__rmv" @click="removeTodo(index)">X</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      message: "",
      todos: [],
    };
  },
  methods: {
    addTodo() {
      this.todos.push({ id: Date.now(), done: false, content: this.message });
      this.message = "";
    },
    toggleDone(todo) {
      todo.done = !todo.done;
    },
    removeTodo(index) {
      if (confirm("Do you want to remove: " + this.todos[index].content)) {
        this.todos.splice(index, 1);
      }
    },
    allDone() {
      this.todos.forEach((element) => {
        element.done = true;
      });
    },
    removeAll() {
      if (confirm("Do you want to remove all To-dos ?")) {
        this.todos = [];
      }
    },
  },
};
</script>

